import React, { Component } from 'react';
import Filter from '../Filter';
import Products from '../Products';
import Bag from '../Bag';
// import Loading from '../Fragments/Loading';
import './style.scss';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filteredItem: []
    };
    passFilteredItem = passFilteredItem.bind(this);
    isUpdateBag = isUpdateBag.bind(this)
  }
  render() {
    return (
      //React.Fragment nhm cc phn t con m khng thm node no mi vo DOM
      <React.Fragment>
        {/* <Loading ref="lds_ring"/> */}
        <Filter passFilteredItem={passFilteredItem}/>
        <Products passFilteredItem={this.state.filteredItem} isUpdateBag={isUpdateBag} loading={this.refs.lds_ring}/>
        <Bag isUpdateBag={this.state.isUpdateBag}/>
      </React.Fragment>
    );
  }
}
function isUpdateBag(response) {
  console.log('bag response: ',response);
  if (response === 'yes') {
    let prevState = this.state;
    prevState.isUpdateBag = 'yes';
    this.setState(prevState);
  }
}
function passFilteredItem (childData) {
  if (childData.length > 0) {
    // JSON.stringify(this.state.filteredItem) === JSON.stringify(childData);
    // console.log(JSON.stringify(this.state.filteredItem) === JSON.stringify(childData));
    // console.log(JSON.stringify(this.state.filteredItem));
    // console.log(JSON.stringify(childData));

    // if(!(JSON.stringify(this.state.filteredItem) === JSON.stringify(childData))) {
    //   this.setState({
    //     filteredItem : childData
    //   });
    // }
    this.setState({
      filteredItem : childData
    });
  } else 
  this.setState({
    filteredItem : []
  });
  // console.log(childData.length)
}
export default App;