function loading(how) {
  if (how === 'show') {
    // console.log(this.props.loading)
    // this.props.loading.style.display = 'block';
    this.refs.lds_ring.style.display = 'block';
  } else {
    // this.props.loading.style.display = 'none';
    this.refs.lds_ring.style.display = 'none';
  }
}
function howSort(products) {
  console.log('da vao howsort');
  let how = this.props.whichSort;
  // let prevProducts = products;
  let newProducts = products;
  switch (how) {
    case 'increment':
      console.log('sapxem tang');
      // sap xep tang dan
      newProducts.sort(function(a, b){return a.price - b.price});
      break;

    case 'decrement':
        console.log('sapxem giamr');
      newProducts.sort(function(a, b){return b.price - a.price});
      break;
  
    default:
      return newProducts
  }
  return newProducts
}
